export class EmployeModel {
  code: string;
  name: string;
  gender: string;
  salarioAnnuale: number;
  annoDiNascita: string;
  constructor(
    code = "",
    name = "",
    gender = "",
    salarioAnnuale = 0,
    annoDiNascita = ""
  ) {
    this.code = code;
    this.name = name;
    this.salarioAnnuale = salarioAnnuale;
    this.annoDiNascita = annoDiNascita;
  }
}
