import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  OnChanges,
  EventEmitter,
  Output
} from "@angular/core";
import { NgForm } from "@angular/forms";
import { EmployeService } from "./../../service/employe.service";

import { EmployeModel } from "src/app/model/employe-model";

@Component({
  selector: "app-employee",
  templateUrl: "./employee.component.html",
  styleUrls: ["./employee.component.css"],
  providers: [EmployeService]
})
export class EmployeeComponent implements OnInit, OnChanges {
  selectedEmploye: EmployeModel;
  @ViewChild("f", { static: true })
  form: NgForm;

  activeChange: boolean;
  constructor(private employeService: EmployeService) {}

  ngOnInit() {
    this.selectedEmploye = new EmployeModel();
    this.activeChange = true;
    this.getAllEmployes();
  }

  onSubmit(f: NgForm) {
    this.employeService.postEmploye(f.value).subscribe(data => {
      console.log(f.value);
      this.getAllEmployes();
      f.form.reset();
    });
  }
  ngOnChanges() {
    this.getAllEmployes();
  }
  getAllEmployes() {
    this.employeService.getEmployes().subscribe(data => {
      this.employeService.employes = data as EmployeModel[];
      this.employeService.employes.sort(this._comparaElemetn);
    });
  }

  _comparaElemetn(a, b) {
    if (a.code < b.code) return -1;
    if (a.code > b.code) return 1;
    else return 0;
  }
  _helperElement(item: EmployeModel, form: NgForm) {
    for (let value in item) {
      if (value != "_id" && value != "__v") {
        form.controls[value].setValue(item[value]);
        console.log(value);
      }
    }
  }
  changeElement(form: NgForm) {
    this.employeService.putEmploye(form.value).subscribe(data => {
      console.log(data);
      this.getAllEmployes();
    });
  }
  seleziona(item: EmployeModel) {
    // this.form.controls["name"].setValue(item.name);
    this.activeChange = false;
    this._helperElement(item, this.form);
  }
  getTargetItem(item: EmployeModel) {
    console.log(item.code);
    // this.form.controls["name"].setValue(item.code);
    if (confirm(`sicuro di voler eliminare ${item.name} ?`)) {
      this.employeService.deleteEmploye(item.code).subscribe(data => {
        console.log(data);
        this.getAllEmployes();
      });
    }
  }
  refreshForm(f: NgForm) {
    this.activeChange = true;
    f.form.reset();
  }
}
