import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { EmployeModel } from "../model/employe-model";

@Injectable({
  providedIn: "root"
})
export class EmployeService {
  employes: EmployeModel[];
  readonly URL_API = "api/Employe";
  constructor(private http: HttpClient) {}

  getEmployes() {
    return this.http.get(this.URL_API);
  }

  postEmploye(employe: EmployeModel) {
    return this.http.post(this.URL_API, employe);
  }

  getEmploye(code: string) {
    return this.http.get(this.URL_API + `/${code}`);
  }

  putEmploye(employe: EmployeModel) {
    return this.http.put(this.URL_API + `/${employe.code}`, employe);
  }

  deleteEmploye(code: string) {
    return this.http.delete(this.URL_API + `/${code}`);
  }
}
